.wrapper {
  width: 100%;
  // height: 3440px;
  background-color: #ffead1;
  display: flex;
  flex-direction: column;
  position: relative;
  overflow: hidden;
  .title {
    span {
      font-family: Quicksand;
      font-size: 72px;
      font-weight: 700;
      line-height: 90px;
      letter-spacing: 0em;
      text-align: left;
      color: #f4627d;
    }
  }
  .text {
    margin-top: calc(369px - 269px - 90px);
    span {
      font-family: Quicksand;
      font-size: 24px;
      font-weight: 700;
      line-height: 30px;
      letter-spacing: 0em;
      text-align: left;
      color: #7b5d58;
    }
  }
  .button {
    width: 443px;
    height: 66px;

    padding: 16px, 36px, 16px, 36px;
    border-radius: 8px;

    background: #f4627d;

    display: flex;
    align-items: center;
    justify-content: center;
    span {
      font-family: Quicksand;
      font-size: 28px;
      font-weight: 700;
      line-height: 66px;
      letter-spacing: 0em;
      text-align: left;
      color: #ffffff;
    }
    :hover{
      cursor: pointer;
      opacity: .8;
    }
  }
  .part_1 {
    height: calc(780px - 147.8px);
    width: 100%;
    display: flex;
    padding: calc(269px - 147.8px) 16px 0;
    .button {
      margin-top: calc(602px - 269px - 90px - 150px);
    }
    .baby {
      width: 50%;
      height: 100%;
      //   background-color: red;
      .image {
        width: 228px;
        height: 326px;
        margin-top: calc(342px - 269px);
        margin-left: calc(783px - 1440px / 2 - 20px);
        border-radius: 18px;
        // border: 10px;
        background: linear-gradient(0deg, #d9d9d9, #d9d9d9),
          linear-gradient(0deg, #ffffff, #ffffff);
        // border: 10px solid #ffffff;
        & img {
          width: 100%;
          height: 100%;
        }
      }
    }
    .parent {
      width: 50%;
      height: 100%;
      //   background-color: red;
      position: relative;

      .father {
        width: 234px;
        height: 234px;
        border-radius: 50%;

        margin-left: calc(1126px - 1440px / 2 - 1440px / 4);
        // transform: rotateX(3.4deg);
        background: linear-gradient(0deg, #d9d9d9, #d9d9d9),
          linear-gradient(0deg, #ffffff, #ffffff);
        // border: 10px solid #ffffff;
      }
      .mother {
        width: 234px;
        height: 234px;
        border-radius: 50%;
        margin-left: calc(1243.63px - 1440px / 2 - 1440px / 4 - 60px);
        margin-top: calc(393px - 269px - 200px);
        // transform: rotateX(25.2deg);
        background: linear-gradient(0deg, #d9d9d9, #d9d9d9),
          linear-gradient(0deg, #ffffff, #ffffff);
        // border: 10px solid #ffffff;
      }
      .arrow {
        position: absolute;
        top: 35%;
        left: -17%;
      }
    }
  }
  .part_2 {
    height: 570px;
    // width: 100%;
    background: linear-gradient(
      99.54deg,
      rgba(244, 98, 125, 0.2) -37.11%,
      rgba(255, 242, 226, 0.2) 132.14%
    );
    display: flex;
    padding: 0 16px;
    .left {
      width: 45%;
      flex-direction: row;
      align-items: center;
      //   justify-content: center;
      .pic {
        width: 185px;
        height: 183px;
        background-color: white;
      }
      .title {
        flex: 1;
        display: flex;
        flex-direction: column;
        .box {
          font-family: Roboto;
          font-size: 130px;
          font-weight: 700;
          line-height: 152px;
          letter-spacing: 0em;
          color: #f7acc0;

          display: flex;
          justify-content: center;
        }
        .icon {
          display: flex;
          justify-content: center;
        }
      }
      .phone {
        width: 156px;
        height: 303px;

        border-radius: 17px;
        // border: 8px;
        border: 8px solid #000000;
      }
    }
    .right {
      flex-direction: column;
      justify-content: center;
      width: 55%;
      padding-left: 32px;
      gap: 20px;
      .button {
        margin-top: 0;
      }
    }
  }
  .part_3 {
    height: calc(2342px - 780px - 570px);
    // width: 100%;
    // background-color: red;

    display: flex;
    flex-direction: column;

    .header {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      :nth-child(1) {
        height: 80px;
        margin-top: calc(1446px - 780px - 570px);

        font-family: Quicksand;
        font-size: 64px;
        font-weight: 700;
        line-height: 80px;
        letter-spacing: 0em;
        color: #6b3e3d;
      }
      :nth-child(2) {
        font-family: Quicksand;
        font-size: 48px;
        font-weight: 700;
        line-height: 60px;
        letter-spacing: 0em;
        text-align: center;
        color: #f4627d;
      }
    }
    .body {
      flex: 1;
      display: flex;
      align-items: center;
      .box_wrapper {
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        padding-left: 215px;
        padding-right: 16px;
        gap: calc(415px - 215px - 173px);
        .box {
          // width: 173px;
          // height: 260px;
          background: #ffffff;
          // border: 2px solid #ffffff;
          // border-radius: 16px;
          box-shadow: 0px 4px 4px 0px #00000040;
        }
      }

      .box {
        width: 173px;
        height: 260px;
        border-radius: 16px;
        & img {
          width: 100%;
          height: 100%;
        }

      }
    }
    :nth-child(2) {
      display: flex;
      .left {
        position: relative;
        width: 40%;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        padding-left: 60px;
        .pic {
          width: 133.33px;
          height: 200px;
          border-radius: 16px;
          // border: 2px;
          transform: rotate(-10.71deg);
          background: #ffffff;
          border: 2px solid #ffffff;
          box-shadow: 0px 4px 4px 0px #00000040;
          & img {
            height: 100%;
            width: 100%;
          }
        }
        :nth-child(3) {
          transform: rotate(10.49deg);
        }
        .arrow {
          position: absolute;
          left: 60px;
          top: 70%;
        }
      }
      .right {
        width: 60%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        padding: 0 16px;
        .button {
          margin-top: 20px;
        }
      }
    }
    :nth-child(3) {
    }
  }
  .part_4 {
    // width: 1440px;
    height: 570px;
    display: flex;
    background: linear-gradient(
      99.54deg,
      rgba(255, 242, 226, 0.2) -37.11%,
      rgba(244, 98, 125, 0.2) 132.14%
    );
    padding:16px;
    gap:8px;
    .button {
      margin-top: 40px;
    }
    .left {
      flex: 1;
      display: flex;
      flex-direction: column;
      justify-content: center;
    }

    .right {
      flex:1;
      display: flex;
      flex-direction: row;
      align-items: center;
      height: 100%;
      .img {
        display: flex;
        flex-direction: column;
        height: 100%;
        justify-content: space-around;
        .top{
          display: flex;
          flex-direction: row;
          transform: translateX(-54px);
          & img {
            transform: rotate(3.42deg);
          }
          :first-child{
            transform: rotate(-20.3deg) translate(40px,25px);
  
          }
  
        }
        .bottom {
          width: 435px;
          height: 227px;
          border-radius: 8px;
          background-color: #FFEAD1;
          display: grid;
          place-items: center;
        }
      }

    }
  }
  .part_5{
    display: flex;
    flex-direction: row;
    align-items: center;
    position: relative;
    padding: 24px;
    .arrow {
      position: absolute;
      right: 46px;
      top: 150px
    }
    .content {
      flex: 1;
      display: grid;
      grid-template-columns: 2fr 1fr;
      column-gap: 8px;
      row-gap: 64px;
      padding:24px;
      .text {
        display: grid;
        place-items: center;
        & span {
          width: 693px;
          height: 180px;
        }

      }
      .img-bf{
        display: flex;
        flex-direction: row;
        & img {
          width: 140px;
          height: 200px;
          border-radius: 8px;
        }
        :first-child{
          transform: rotate(-20.3deg) translate(20px, 20px);
        }
      }
      .result {
        grid-column: 1/3;
        display: flex;
        flex-direction: row;
        justify-content: center;
        gap: 8px;
        & img {
          width: 140px;
          height: 200px;
          border-radius: 8px;
        }
        
      }
      .btn {
        grid-column: 1/3;
        display: flex;
        flex-direction: row;
        justify-content: center;
        margin-top: 24px;

      }
    }
  }
  .left {
    width: 50%;
    height: 100%;
    display: flex;
    flex-direction: column;
  }
  .right {
    width: 50%;
    height: 100%;
    display: flex;
  }
}
a {
  text-decoration: none;
}

@media (max-width: 1260px) {
  .wrapper {
    .part_1 {
      .left {
        scale: 0.9;
      }
      .right {
        scale: 0.8;
        margin-right: 20px;
        margin-bottom: 20px;

        .baby {
          .image {
            margin-left: -20px;
          }
        }
        .parent {
          margin-left: -20px;

          .arrow {
            top: 32.5%;
            left: -18.5%;
          }
        }
      }
    }
    .part_2 {
      .left {
        .pic {
          width: 100px;
          height: 100px;
        }
        .title {
          width: 30%;
          scale: 0.8;
        }
      }
      .right {
        scale: 0.8;
      }
    }
    .part_3 {
      .header {
        scale: 0.9;
      }
      .body {
        .box_wrapper {
          // padding-left: 100px;
          .box {
            height: 180px;
            width: 120px;
          }
          :nth-child(6) {
            display: none;
          }
        }
      }
      :nth-child(2) {
        .left {
          scale: 0.9;

          .arrow {
            scale: 0.7;
          }
        }
        .right {
          scale: 0.9;
        }
      }
    }
    .part_4 {
      .button {
        margin-top: 20px;
      }
      .left {
        justify-content: center;
      scale: 0.9;

      }
    }
  }
}
